/**
 * UserProfilePage - דף פרופיל משתמש מקיף
 *
 * מאפשר למשתמש לראות ולערוך:
 * - זהות מקצועית
 * - העדפות עבודה
 * - שופטים ועורכי דין יריבים
 * - מה יתרו למד עליו
 * - סטטיסטיקות שימוש
 */

import React, { useState, useEffect } from 'react';
import { useSearchParams } from 'react-router-dom';
import {
  User,
  Settings,
  Users,
  Brain,
  BarChart3,
  Save,
  Plus,
  Trash2,
  CheckCircle,
  AlertCircle,
  Briefcase,
  Scale,
  MapPin,
  Clock,
  ThumbsUp,
  ThumbsDown,
  RefreshCw,
  Sparkles
} from 'lucide-react';
import { toast } from 'sonner';
import { apiGet, apiPut, apiPost, apiDelete } from '../utils/api';

// Types
interface Judge {
  name: string;
  court?: string;
  sentiment: 'positive' | 'neutral' | 'challenging';
  notes?: string;
  cases_count?: number;
  win_rate?: number;
  last_appearance?: string;
}

interface OpposingLawyer {
  name: string;
  firm?: string;
  sentiment: 'challenging' | 'professional' | 'friendly';
  style?: 'aggressive' | 'cooperative' | 'unpredictable';
  notes?: string;
  cases_count?: number;
  last_case?: string;
}

interface UserProfile {
  id: string;
  user_id: string;
  professional_identity: {
    display_name?: string;
    firm_name?: string;
    role?: string;
    years_experience?: number;
    specializations?: string[];
    bar_number?: string;
    courts_practiced?: string[];
  };
  work_preferences: {
    response_length?: 'concise' | 'balanced' | 'detailed';
    formality_level?: 'formal' | 'semi_formal' | 'casual';
    time_pressure_mode?: boolean;
    preferred_output_format?: 'bullets' | 'narrative' | 'structured';
    include_citations?: boolean;
    hebrew_only?: boolean;
    wants_explanations?: boolean;
    proactive_suggestions?: boolean;
  };
  professional_relationships: {
    judges?: Judge[];
    opposing_lawyers?: OpposingLawyer[];
  };
  feedback_history: {
    explicit_preferences?: { preference: string; source: string }[];
    positive_feedback_patterns?: string[];
    negative_feedback_patterns?: string[];
  };
  usage_patterns: {
    total_cases_analyzed?: number;
    total_documents_processed?: number;
    preferred_tools?: string[];
    common_case_types?: string[];
  };
  interaction_count: number;
  learning_enabled: boolean;
}

// Tab definitions
const TABS = [
  { id: 'identity', label: 'זהות מקצועית', icon: User },
  { id: 'preferences', label: 'העדפות', icon: Settings },
  { id: 'relationships', label: 'שופטים ויריבים', icon: Users },
  { id: 'learning', label: 'מה למדתי', icon: Brain },
  { id: 'stats', label: 'סטטיסטיקות', icon: BarChart3 },
];

// Specialization options
const SPECIALIZATIONS = [
  'נזיקין', 'רשלנות רפואית', 'תאונות דרכים', 'ביטוח',
  'דיני עבודה', 'מקרקעין', 'משפחה', 'פלילי', 'מסחרי', 'חברות'
];

// Court options
const COURTS = [
  'שלום ת"א', 'שלום חיפה', 'שלום ירושלים', 'שלום ב"ש',
  'מחוזי ת"א', 'מחוזי חיפה', 'מחוזי ירושלים', 'מחוזי ב"ש',
  'עליון', 'עבודה', 'משפחה'
];

// Role options
const ROLES = [
  'עורך/ת דין',
  'שופט/ת',
  'פרקליט/ה',
  'יועץ/ת משפטי/ת',
  'סטודנט/ית למשפטים',
  'מתמחה'
];

export default function UserProfilePage() {
  const [searchParams] = useSearchParams();
  const [profile, setProfile] = useState<UserProfile | null>(null);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [activeTab, setActiveTab] = useState(searchParams.get('tab') || 'identity');
  const [hasChanges, setHasChanges] = useState(false);

  // Edit states
  const [showAddJudge, setShowAddJudge] = useState(false);
  const [showAddLawyer, setShowAddLawyer] = useState(false);

  // Load profile
  useEffect(() => {
    loadProfile();
  }, []);

  const loadProfile = async () => {
    try {
      setLoading(true);
      const data = await apiGet('/api/user-profile');
      setProfile(data.profile);
    } catch (error) {
      console.error('Failed to load profile:', error);
      toast.error('שגיאה בטעינת הפרופיל');
    } finally {
      setLoading(false);
    }
  };

  const saveProfile = async () => {
    if (!profile) return;

    try {
      setSaving(true);
      await apiPut('/api/user-profile', {
        professional_identity: profile.professional_identity,
        work_preferences: profile.work_preferences,
        learning_enabled: profile.learning_enabled,
      });
      toast.success('הפרופיל נשמר בהצלחה');
      setHasChanges(false);
    } catch (error) {
      console.error('Failed to save profile:', error);
      toast.error('שגיאה בשמירת הפרופיל');
    } finally {
      setSaving(false);
    }
  };

  const updateIdentity = (field: string, value: any) => {
    if (!profile) return;
    setProfile({
      ...profile,
      professional_identity: {
        ...profile.professional_identity,
        [field]: value
      }
    });
    setHasChanges(true);
  };

  const updatePreferences = (field: string, value: any) => {
    if (!profile) return;
    setProfile({
      ...profile,
      work_preferences: {
        ...profile.work_preferences,
        [field]: value
      }
    });
    setHasChanges(true);
  };

  const toggleSpecialization = (spec: string) => {
    if (!profile) return;
    const current = profile.professional_identity.specializations || [];
    const updated = current.includes(spec)
      ? current.filter(s => s !== spec)
      : [...current, spec].slice(0, 5); // Max 5
    updateIdentity('specializations', updated);
  };

  const toggleCourt = (court: string) => {
    if (!profile) return;
    const current = profile.professional_identity.courts_practiced || [];
    const updated = current.includes(court)
      ? current.filter(c => c !== court)
      : [...current, court];
    updateIdentity('courts_practiced', updated);
  };

  const addJudge = async (judge: Judge) => {
    try {
      await apiPost('/api/user-profile/judges', judge);
      toast.success(`השופט ${judge.name} נוסף בהצלחה`);
      loadProfile();
      setShowAddJudge(false);
    } catch (error) {
      toast.error('שגיאה בהוספת שופט');
    }
  };

  const removeJudge = async (name: string) => {
    try {
      await apiDelete(`/api/user-profile/judges/${encodeURIComponent(name)}`);
      toast.success('השופט הוסר');
      loadProfile();
    } catch (error) {
      toast.error('שגיאה בהסרת שופט');
    }
  };

  const addLawyer = async (lawyer: OpposingLawyer) => {
    try {
      await apiPost('/api/user-profile/opposing-lawyers', lawyer);
      toast.success(`עו"ד ${lawyer.name} נוסף בהצלחה`);
      loadProfile();
      setShowAddLawyer(false);
    } catch (error) {
      toast.error('שגיאה בהוספת עורך דין');
    }
  };

  const removeLawyer = async (name: string) => {
    try {
      await apiDelete(`/api/user-profile/opposing-lawyers/${encodeURIComponent(name)}`);
      toast.success('עורך הדין הוסר');
      loadProfile();
    } catch (error) {
      toast.error('שגיאה בהסרת עורך דין');
    }
  };

  const resetLearning = async () => {
    if (!window.confirm('האם אתה בטוח? פעולה זו תאפס את כל מה שיתרו למד עליך (מלבד הזהות המקצועית).')) {
      return;
    }
    try {
      await apiPost('/api/user-profile/reset', {});
      toast.success('הלמידה אופסה בהצלחה');
      loadProfile();
    } catch (error) {
      toast.error('שגיאה באיפוס');
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  if (!profile) {
    return (
      <div className="flex items-center justify-center h-screen text-white">
        <AlertCircle className="mr-2" /> שגיאה בטעינת הפרופיל
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 p-4 md:p-8" dir="rtl">
      <div className="max-w-5xl mx-auto">

        {/* Header */}
        <div className="bg-white/10 backdrop-blur-xl rounded-2xl p-6 mb-6 border border-white/20">
          <div className="flex items-start justify-between">
            <div className="flex items-center gap-4">
              <div className="w-16 h-16 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center text-white text-2xl font-bold">
                {(profile.professional_identity.display_name || 'U')[0]}
              </div>
              <div>
                <h1 className="text-2xl font-bold text-white">
                  {profile.professional_identity.display_name || 'המשתמש שלי'}
                </h1>
                <p className="text-blue-300">
                  {profile.professional_identity.firm_name && `${profile.professional_identity.firm_name} | `}
                  {profile.professional_identity.years_experience && `${profile.professional_identity.years_experience} שנות ניסיון`}
                </p>
                {profile.professional_identity.specializations?.length > 0 && (
                  <p className="text-gray-400 text-sm mt-1">
                    התמחויות: {profile.professional_identity.specializations.join(', ')}
                  </p>
                )}
              </div>
            </div>

            {hasChanges && (
              <button
                onClick={saveProfile}
                disabled={saving}
                className="flex items-center gap-2 px-4 py-2 bg-gradient-to-r from-green-500 to-emerald-600 text-white rounded-lg hover:opacity-90 transition-opacity disabled:opacity-50"
              >
                {saving ? (
                  <RefreshCw className="animate-spin" size={18} />
                ) : (
                  <Save size={18} />
                )}
                שמור שינויים
              </button>
            )}
          </div>
        </div>

        {/* Tabs */}
        <div className="flex gap-2 mb-6 overflow-x-auto pb-2">
          {TABS.map(tab => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`flex items-center gap-2 px-4 py-2 rounded-lg whitespace-nowrap transition-all ${
                activeTab === tab.id
                  ? 'bg-blue-600 text-white'
                  : 'bg-white/10 text-gray-300 hover:bg-white/20'
              }`}
            >
              <tab.icon size={18} />
              {tab.label}
            </button>
          ))}
        </div>

        {/* Tab Content */}
        <div className="bg-white/10 backdrop-blur-xl rounded-2xl p-6 border border-white/20">

          {/* Identity Tab */}
          {activeTab === 'identity' && (
            <div className="space-y-6">
              <h2 className="text-xl font-bold text-white flex items-center gap-2">
                <User size={24} /> זהות מקצועית
              </h2>

              <div className="grid md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-blue-200 text-sm mb-2">שם מלא (כפי שיופיע)</label>
                  <input
                    type="text"
                    value={profile.professional_identity.display_name || ''}
                    onChange={(e) => updateIdentity('display_name', e.target.value)}
                    placeholder='עו"ד ישראל ישראלי'
                    className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-500 focus:border-blue-500 focus:outline-none"
                  />
                </div>

                <div>
                  <label className="block text-blue-200 text-sm mb-2">שם המשרד</label>
                  <input
                    type="text"
                    value={profile.professional_identity.firm_name || ''}
                    onChange={(e) => updateIdentity('firm_name', e.target.value)}
                    placeholder="משרד עורכי דין..."
                    className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-500 focus:border-blue-500 focus:outline-none"
                  />
                </div>

                <div>
                  <label className="block text-blue-200 text-sm mb-2">תפקיד</label>
                  <select
                    value={profile.professional_identity.role || ''}
                    onChange={(e) => updateIdentity('role', e.target.value)}
                    className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white focus:border-blue-500 focus:outline-none"
                  >
                    <option value="">בחר תפקיד...</option>
                    {ROLES.map(role => (
                      <option key={role} value={role}>{role}</option>
                    ))}
                  </select>
                </div>

                <div>
                  <label className="block text-blue-200 text-sm mb-2">שנות ניסיון</label>
                  <input
                    type="number"
                    value={profile.professional_identity.years_experience || ''}
                    onChange={(e) => updateIdentity('years_experience', parseInt(e.target.value) || 0)}
                    placeholder="0"
                    min="0"
                    max="60"
                    className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-500 focus:border-blue-500 focus:outline-none"
                  />
                </div>

                <div>
                  <label className="block text-blue-200 text-sm mb-2">מספר רישיון</label>
                  <input
                    type="text"
                    value={profile.professional_identity.bar_number || ''}
                    onChange={(e) => updateIdentity('bar_number', e.target.value)}
                    placeholder="12345"
                    className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-500 focus:border-blue-500 focus:outline-none"
                  />
                </div>
              </div>

              <div>
                <label className="block text-blue-200 text-sm mb-3">התמחויות (בחר עד 5)</label>
                <div className="flex flex-wrap gap-2">
                  {SPECIALIZATIONS.map(spec => (
                    <button
                      key={spec}
                      onClick={() => toggleSpecialization(spec)}
                      className={`px-3 py-1.5 rounded-full text-sm transition-all ${
                        profile.professional_identity.specializations?.includes(spec)
                          ? 'bg-blue-600 text-white'
                          : 'bg-white/10 text-gray-300 hover:bg-white/20'
                      }`}
                    >
                      {spec}
                    </button>
                  ))}
                </div>
              </div>

              <div>
                <label className="block text-blue-200 text-sm mb-3">בתי משפט (איפה אתה פועל)</label>
                <div className="flex flex-wrap gap-2">
                  {COURTS.map(court => (
                    <button
                      key={court}
                      onClick={() => toggleCourt(court)}
                      className={`px-3 py-1.5 rounded-full text-sm transition-all ${
                        profile.professional_identity.courts_practiced?.includes(court)
                          ? 'bg-purple-600 text-white'
                          : 'bg-white/10 text-gray-300 hover:bg-white/20'
                      }`}
                    >
                      {court}
                    </button>
                  ))}
                </div>
              </div>
            </div>
          )}

          {/* Preferences Tab */}
          {activeTab === 'preferences' && (
            <div className="space-y-6">
              <h2 className="text-xl font-bold text-white flex items-center gap-2">
                <Settings size={24} /> איך אתה אוהב לעבוד?
              </h2>

              <div>
                <label className="block text-blue-200 text-sm mb-3">אורך תשובות</label>
                <div className="grid grid-cols-3 gap-3">
                  {[
                    { value: 'concise', label: 'קצר ותמציתי', desc: 'רק השורה התחתונה' },
                    { value: 'balanced', label: 'מאוזן', desc: 'איזון מומלץ' },
                    { value: 'detailed', label: 'מפורט', desc: 'כל הפרטים' },
                  ].map(option => (
                    <button
                      key={option.value}
                      onClick={() => updatePreferences('response_length', option.value)}
                      className={`p-4 rounded-xl text-center transition-all ${
                        profile.work_preferences.response_length === option.value
                          ? 'bg-blue-600 text-white border-2 border-blue-400'
                          : 'bg-white/10 text-gray-300 hover:bg-white/20 border-2 border-transparent'
                      }`}
                    >
                      <div className="font-semibold">{option.label}</div>
                      <div className="text-xs mt-1 opacity-70">{option.desc}</div>
                    </button>
                  ))}
                </div>
              </div>

              <div>
                <label className="block text-blue-200 text-sm mb-3">סגנון</label>
                <div className="grid grid-cols-3 gap-3">
                  {[
                    { value: 'formal', label: 'פורמלי', desc: 'מתאים לבית משפט' },
                    { value: 'semi_formal', label: 'חצי-פורמלי', desc: 'מקצועי אך נגיש' },
                    { value: 'casual', label: 'ישיר', desc: 'תכלס ולעניין' },
                  ].map(option => (
                    <button
                      key={option.value}
                      onClick={() => updatePreferences('formality_level', option.value)}
                      className={`p-4 rounded-xl text-center transition-all ${
                        profile.work_preferences.formality_level === option.value
                          ? 'bg-purple-600 text-white border-2 border-purple-400'
                          : 'bg-white/10 text-gray-300 hover:bg-white/20 border-2 border-transparent'
                      }`}
                    >
                      <div className="font-semibold">{option.label}</div>
                      <div className="text-xs mt-1 opacity-70">{option.desc}</div>
                    </button>
                  ))}
                </div>
              </div>

              <div>
                <label className="block text-blue-200 text-sm mb-3">פורמט פלט מועדף</label>
                <div className="grid grid-cols-3 gap-3">
                  {[
                    { value: 'bullets', label: 'נקודות', desc: 'רשימה ממוספרת' },
                    { value: 'structured', label: 'מובנה', desc: 'כותרות וסעיפים' },
                    { value: 'narrative', label: 'רציף', desc: 'טקסט זורם' },
                  ].map(option => (
                    <button
                      key={option.value}
                      onClick={() => updatePreferences('preferred_output_format', option.value)}
                      className={`p-4 rounded-xl text-center transition-all ${
                        profile.work_preferences.preferred_output_format === option.value
                          ? 'bg-emerald-600 text-white border-2 border-emerald-400'
                          : 'bg-white/10 text-gray-300 hover:bg-white/20 border-2 border-transparent'
                      }`}
                    >
                      <div className="font-semibold">{option.label}</div>
                      <div className="text-xs mt-1 opacity-70">{option.desc}</div>
                    </button>
                  ))}
                </div>
              </div>

              <div className="space-y-3">
                <label className="block text-blue-200 text-sm">אפשרויות נוספות</label>

                {[
                  { key: 'include_citations', label: 'תמיד כלול מקורות והפניות', desc: 'הפניות למסמכים ועמודים' },
                  { key: 'wants_explanations', label: 'הוסף הסברים למסקנות', desc: 'נימוקים לכל המלצה' },
                  { key: 'hebrew_only', label: 'הימנע ממונחים באנגלית', desc: 'עברית בלבד' },
                  { key: 'proactive_suggestions', label: 'הצע הצעות יזומות', desc: 'יתרו יציע לפני שתבקש' },
                ].map(option => (
                  <div
                    key={option.key}
                    className="flex items-center justify-between p-3 bg-white/5 rounded-lg"
                  >
                    <div>
                      <div className="text-white font-medium">{option.label}</div>
                      <div className="text-gray-400 text-sm">{option.desc}</div>
                    </div>
                    <button
                      onClick={() => updatePreferences(option.key, !profile.work_preferences[option.key as keyof typeof profile.work_preferences])}
                      className={`w-12 h-6 rounded-full transition-colors ${
                        profile.work_preferences[option.key as keyof typeof profile.work_preferences]
                          ? 'bg-green-500'
                          : 'bg-gray-600'
                      }`}
                    >
                      <div className={`w-5 h-5 bg-white rounded-full transition-transform ${
                        profile.work_preferences[option.key as keyof typeof profile.work_preferences]
                          ? 'translate-x-6'
                          : 'translate-x-1'
                      }`} />
                    </button>
                  </div>
                ))}
              </div>

              <div className="p-4 bg-yellow-500/20 rounded-xl border border-yellow-500/30">
                <div className="flex items-center justify-between">
                  <div>
                    <div className="text-yellow-300 font-semibold flex items-center gap-2">
                      <Clock size={18} /> מצב לחץ זמן
                    </div>
                    <div className="text-yellow-200/70 text-sm">
                      הפעל כשיש דדליין קרוב - תשובות מהירות ותמציתיות יותר
                    </div>
                  </div>
                  <button
                    onClick={() => updatePreferences('time_pressure_mode', !profile.work_preferences.time_pressure_mode)}
                    className={`px-4 py-2 rounded-lg font-medium transition-all ${
                      profile.work_preferences.time_pressure_mode
                        ? 'bg-yellow-500 text-black'
                        : 'bg-white/10 text-white'
                    }`}
                  >
                    {profile.work_preferences.time_pressure_mode ? 'מופעל' : 'כבוי'}
                  </button>
                </div>
              </div>
            </div>
          )}

          {/* Relationships Tab */}
          {activeTab === 'relationships' && (
            <div className="space-y-6">
              {/* Judges Section */}
              <div>
                <div className="flex items-center justify-between mb-4">
                  <h2 className="text-xl font-bold text-white flex items-center gap-2">
                    <Scale size={24} /> שופטים שאני מכיר
                  </h2>
                  <button
                    onClick={() => setShowAddJudge(true)}
                    className="flex items-center gap-2 px-3 py-1.5 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors"
                  >
                    <Plus size={16} /> הוסף שופט
                  </button>
                </div>

                {profile.professional_relationships.judges?.length === 0 ? (
                  <div className="text-center py-8 text-gray-400">
                    <Scale size={48} className="mx-auto mb-3 opacity-50" />
                    <p>עדיין לא הוספת שופטים</p>
                    <p className="text-sm">שופטים יתווספו אוטומטית מהתיקים שלך</p>
                  </div>
                ) : (
                  <div className="space-y-3">
                    {profile.professional_relationships.judges?.map((judge, idx) => (
                      <div key={idx} className="p-4 bg-white/5 rounded-xl border border-white/10">
                        <div className="flex items-start justify-between">
                          <div>
                            <div className="flex items-center gap-2">
                              <span className="text-white font-semibold">{judge.name}</span>
                              <span className={`px-2 py-0.5 rounded-full text-xs ${
                                judge.sentiment === 'positive' ? 'bg-green-500/20 text-green-400' :
                                judge.sentiment === 'challenging' ? 'bg-red-500/20 text-red-400' :
                                'bg-gray-500/20 text-gray-400'
                              }`}>
                                {judge.sentiment === 'positive' ? 'חיובי' :
                                 judge.sentiment === 'challenging' ? 'מאתגר' : 'ניטרלי'}
                              </span>
                            </div>
                            {judge.court && (
                              <div className="text-blue-300 text-sm flex items-center gap-1 mt-1">
                                <MapPin size={14} /> {judge.court}
                              </div>
                            )}
                            {judge.notes && (
                              <div className="text-gray-400 text-sm mt-2">
                                📝 {judge.notes}
                              </div>
                            )}
                            <div className="flex gap-4 mt-2 text-xs text-gray-500">
                              {judge.cases_count && <span>{judge.cases_count} תיקים</span>}
                              {judge.win_rate !== undefined && <span>{Math.round(judge.win_rate * 100)}% הצלחה</span>}
                              {judge.last_appearance && <span>אחרון: {judge.last_appearance}</span>}
                            </div>
                          </div>
                          <button
                            onClick={() => removeJudge(judge.name)}
                            className="p-2 text-red-400 hover:bg-red-500/20 rounded-lg transition-colors"
                          >
                            <Trash2 size={16} />
                          </button>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </div>

              {/* Opposing Lawyers Section */}
              <div className="pt-6 border-t border-white/10">
                <div className="flex items-center justify-between mb-4">
                  <h2 className="text-xl font-bold text-white flex items-center gap-2">
                    <Briefcase size={24} /> עורכי דין יריבים
                  </h2>
                  <button
                    onClick={() => setShowAddLawyer(true)}
                    className="flex items-center gap-2 px-3 py-1.5 bg-purple-600 text-white rounded-lg hover:bg-purple-700 transition-colors"
                  >
                    <Plus size={16} /> הוסף יריב
                  </button>
                </div>

                {profile.professional_relationships.opposing_lawyers?.length === 0 ? (
                  <div className="text-center py-8 text-gray-400">
                    <Briefcase size={48} className="mx-auto mb-3 opacity-50" />
                    <p>עדיין לא הוספת עורכי דין יריבים</p>
                    <p className="text-sm">יריבים יתווספו אוטומטית מהתיקים שלך</p>
                  </div>
                ) : (
                  <div className="space-y-3">
                    {profile.professional_relationships.opposing_lawyers?.map((lawyer, idx) => (
                      <div key={idx} className="p-4 bg-white/5 rounded-xl border border-white/10">
                        <div className="flex items-start justify-between">
                          <div>
                            <div className="flex items-center gap-2">
                              <span className="text-white font-semibold">{lawyer.name}</span>
                              <span className={`px-2 py-0.5 rounded-full text-xs ${
                                lawyer.sentiment === 'friendly' ? 'bg-green-500/20 text-green-400' :
                                lawyer.sentiment === 'challenging' ? 'bg-red-500/20 text-red-400' :
                                'bg-blue-500/20 text-blue-400'
                              }`}>
                                {lawyer.sentiment === 'friendly' ? 'ידידותי' :
                                 lawyer.sentiment === 'challenging' ? 'מאתגר' : 'מקצועי'}
                              </span>
                              {lawyer.style && (
                                <span className="px-2 py-0.5 rounded-full text-xs bg-gray-500/20 text-gray-400">
                                  {lawyer.style === 'aggressive' ? 'אגרסיבי' :
                                   lawyer.style === 'cooperative' ? 'שיתופי' : 'בלתי צפוי'}
                                </span>
                              )}
                            </div>
                            {lawyer.firm && (
                              <div className="text-purple-300 text-sm mt-1">{lawyer.firm}</div>
                            )}
                            {lawyer.notes && (
                              <div className="text-gray-400 text-sm mt-2">
                                📝 {lawyer.notes}
                              </div>
                            )}
                            <div className="flex gap-4 mt-2 text-xs text-gray-500">
                              {lawyer.cases_count && <span>{lawyer.cases_count} תיקים</span>}
                              {lawyer.last_case && <span>אחרון: {lawyer.last_case}</span>}
                            </div>
                          </div>
                          <button
                            onClick={() => removeLawyer(lawyer.name)}
                            className="p-2 text-red-400 hover:bg-red-500/20 rounded-lg transition-colors"
                          >
                            <Trash2 size={16} />
                          </button>
                        </div>
                      </div>
                    ))}
                  </div>
                )}
              </div>

              {/* Add Judge Modal */}
              {showAddJudge && (
                <AddJudgeModal
                  onAdd={addJudge}
                  onClose={() => setShowAddJudge(false)}
                />
              )}

              {/* Add Lawyer Modal */}
              {showAddLawyer && (
                <AddLawyerModal
                  onAdd={addLawyer}
                  onClose={() => setShowAddLawyer(false)}
                />
              )}
            </div>
          )}

          {/* Learning Tab */}
          {activeTab === 'learning' && (
            <div className="space-y-6">
              <div className="flex items-center justify-between">
                <h2 className="text-xl font-bold text-white flex items-center gap-2">
                  <Brain size={24} /> מה יתרו למד עליך
                </h2>
                <button
                  onClick={resetLearning}
                  className="flex items-center gap-2 px-3 py-1.5 text-red-400 hover:bg-red-500/20 rounded-lg transition-colors"
                >
                  <RefreshCw size={16} /> אפס למידה
                </button>
              </div>

              {/* Positive patterns */}
              {profile.feedback_history.positive_feedback_patterns?.length > 0 && (
                <div className="p-4 bg-green-500/10 rounded-xl border border-green-500/20">
                  <h3 className="text-green-400 font-semibold mb-3 flex items-center gap-2">
                    <ThumbsUp size={18} /> דברים שאתה אוהב
                  </h3>
                  <ul className="space-y-2">
                    {profile.feedback_history.positive_feedback_patterns.map((pattern, idx) => (
                      <li key={idx} className="text-green-300 flex items-center gap-2">
                        <CheckCircle size={14} /> {pattern}
                      </li>
                    ))}
                  </ul>
                </div>
              )}

              {/* Negative patterns */}
              {profile.feedback_history.negative_feedback_patterns?.length > 0 && (
                <div className="p-4 bg-red-500/10 rounded-xl border border-red-500/20">
                  <h3 className="text-red-400 font-semibold mb-3 flex items-center gap-2">
                    <ThumbsDown size={18} /> דברים שפחות עובדים לך
                  </h3>
                  <ul className="space-y-2">
                    {profile.feedback_history.negative_feedback_patterns.map((pattern, idx) => (
                      <li key={idx} className="text-red-300 flex items-center gap-2">
                        <AlertCircle size={14} /> {pattern}
                      </li>
                    ))}
                  </ul>
                </div>
              )}

              {/* Explicit preferences */}
              <div className="p-4 bg-blue-500/10 rounded-xl border border-blue-500/20">
                <h3 className="text-blue-400 font-semibold mb-3 flex items-center gap-2">
                  <Sparkles size={18} /> העדפות מפורשות ששמרת
                </h3>
                {profile.feedback_history.explicit_preferences?.length > 0 ? (
                  <ul className="space-y-2">
                    {profile.feedback_history.explicit_preferences.map((pref, idx) => (
                      <li key={idx} className="text-blue-300 flex items-center justify-between">
                        <span className="flex items-center gap-2">
                          <CheckCircle size={14} /> {pref.preference}
                        </span>
                        <span className="text-xs text-gray-500">{pref.source}</span>
                      </li>
                    ))}
                  </ul>
                ) : (
                  <p className="text-gray-400 text-sm">
                    אפשר להוסיף העדפות מפורשות דרך הצ'אט או כאן
                  </p>
                )}
              </div>

              {/* Learning status */}
              <div className="p-4 bg-white/5 rounded-xl">
                <div className="flex items-center justify-between">
                  <div>
                    <div className="text-white font-medium">למידה אוטומטית</div>
                    <div className="text-gray-400 text-sm">
                      יתרו לומד מהמשוב שלך ומשפר את התשובות
                    </div>
                  </div>
                  <button
                    onClick={() => {
                      setProfile({ ...profile, learning_enabled: !profile.learning_enabled });
                      setHasChanges(true);
                    }}
                    className={`w-12 h-6 rounded-full transition-colors ${
                      profile.learning_enabled ? 'bg-green-500' : 'bg-gray-600'
                    }`}
                  >
                    <div className={`w-5 h-5 bg-white rounded-full transition-transform ${
                      profile.learning_enabled ? 'translate-x-6' : 'translate-x-1'
                    }`} />
                  </button>
                </div>
              </div>
            </div>
          )}

          {/* Stats Tab */}
          {activeTab === 'stats' && (
            <div className="space-y-6">
              <h2 className="text-xl font-bold text-white flex items-center gap-2">
                <BarChart3 size={24} /> הסטטיסטיקות שלך
              </h2>

              <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                <div className="p-4 bg-blue-500/20 rounded-xl text-center">
                  <div className="text-3xl font-bold text-blue-400">
                    {profile.usage_patterns.total_cases_analyzed || 0}
                  </div>
                  <div className="text-blue-300 text-sm">תיקים נותחו</div>
                </div>
                <div className="p-4 bg-purple-500/20 rounded-xl text-center">
                  <div className="text-3xl font-bold text-purple-400">
                    {profile.usage_patterns.total_documents_processed || 0}
                  </div>
                  <div className="text-purple-300 text-sm">מסמכים עובדו</div>
                </div>
                <div className="p-4 bg-green-500/20 rounded-xl text-center">
                  <div className="text-3xl font-bold text-green-400">
                    {profile.interaction_count || 0}
                  </div>
                  <div className="text-green-300 text-sm">שאלות נשאלו</div>
                </div>
                <div className="p-4 bg-orange-500/20 rounded-xl text-center">
                  <div className="text-3xl font-bold text-orange-400">
                    {profile.professional_relationships.judges?.length || 0}
                  </div>
                  <div className="text-orange-300 text-sm">שופטים מוכרים</div>
                </div>
              </div>

              {/* Preferred tools */}
              {profile.usage_patterns.preferred_tools?.length > 0 && (
                <div className="p-4 bg-white/5 rounded-xl">
                  <h3 className="text-white font-semibold mb-3">🏆 הכלים האהובים עליך</h3>
                  <div className="space-y-2">
                    {profile.usage_patterns.preferred_tools.slice(0, 5).map((tool, idx) => (
                      <div key={idx} className="flex items-center gap-3">
                        <span className="text-yellow-400">{idx + 1}.</span>
                        <span className="text-gray-300">{tool}</span>
                      </div>
                    ))}
                  </div>
                </div>
              )}

              {/* Common case types */}
              {profile.usage_patterns.common_case_types?.length > 0 && (
                <div className="p-4 bg-white/5 rounded-xl">
                  <h3 className="text-white font-semibold mb-3">📈 סוגי תיקים נפוצים</h3>
                  <div className="space-y-2">
                    {profile.usage_patterns.common_case_types.map((type, idx) => (
                      <div key={idx} className="flex items-center gap-2">
                        <div className="flex-1 bg-white/10 rounded-full h-2">
                          <div
                            className="bg-blue-500 rounded-full h-2"
                            style={{ width: `${100 - idx * 20}%` }}
                          />
                        </div>
                        <span className="text-gray-300 text-sm w-32">{type}</span>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

// Add Judge Modal Component
function AddJudgeModal({ onAdd, onClose }: { onAdd: (judge: Judge) => void; onClose: () => void }) {
  const [name, setName] = useState('');
  const [court, setCourt] = useState('');
  const [sentiment, setSentiment] = useState<'positive' | 'neutral' | 'challenging'>('neutral');
  const [notes, setNotes] = useState('');

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!name.trim()) {
      toast.error('נא להזין שם שופט');
      return;
    }
    onAdd({ name, court, sentiment, notes });
  };

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
      <div className="bg-slate-800 rounded-2xl p-6 max-w-md w-full border border-white/20">
        <h3 className="text-xl font-bold text-white mb-4">הוספת שופט</h3>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block text-blue-200 text-sm mb-2">שם השופט/ת *</label>
            <input
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder="כב' השופט/ת..."
              className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-500"
              autoFocus
            />
          </div>

          <div>
            <label className="block text-blue-200 text-sm mb-2">בית משפט</label>
            <select
              value={court}
              onChange={(e) => setCourt(e.target.value)}
              className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white"
            >
              <option value="">בחר...</option>
              {COURTS.map(c => <option key={c} value={c}>{c}</option>)}
            </select>
          </div>

          <div>
            <label className="block text-blue-200 text-sm mb-2">יחס</label>
            <div className="grid grid-cols-3 gap-2">
              {[
                { value: 'positive', label: 'חיובי', color: 'green' },
                { value: 'neutral', label: 'ניטרלי', color: 'gray' },
                { value: 'challenging', label: 'מאתגר', color: 'red' },
              ].map(opt => (
                <button
                  key={opt.value}
                  type="button"
                  onClick={() => setSentiment(opt.value as any)}
                  className={`p-2 rounded-lg text-sm transition-all ${
                    sentiment === opt.value
                      ? `bg-${opt.color}-500/30 text-${opt.color}-400 border border-${opt.color}-500`
                      : 'bg-white/10 text-gray-300'
                  }`}
                >
                  {opt.label}
                </button>
              ))}
            </div>
          </div>

          <div>
            <label className="block text-blue-200 text-sm mb-2">הערות</label>
            <textarea
              value={notes}
              onChange={(e) => setNotes(e.target.value)}
              placeholder="מה כדאי לזכור על השופט..."
              className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-500 h-20"
            />
          </div>

          <div className="flex gap-3 pt-2">
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-4 py-2 bg-white/10 text-white rounded-lg hover:bg-white/20"
            >
              ביטול
            </button>
            <button
              type="submit"
              className="flex-1 px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700"
            >
              הוסף
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

// Add Lawyer Modal Component
function AddLawyerModal({ onAdd, onClose }: { onAdd: (lawyer: OpposingLawyer) => void; onClose: () => void }) {
  const [name, setName] = useState('');
  const [firm, setFirm] = useState('');
  const [sentiment, setSentiment] = useState<'challenging' | 'professional' | 'friendly'>('professional');
  const [style, setStyle] = useState<'aggressive' | 'cooperative' | 'unpredictable' | ''>('');
  const [notes, setNotes] = useState('');

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!name.trim()) {
      toast.error('נא להזין שם עורך דין');
      return;
    }
    onAdd({ name, firm, sentiment, style: style || undefined, notes });
  };

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
      <div className="bg-slate-800 rounded-2xl p-6 max-w-md w-full border border-white/20">
        <h3 className="text-xl font-bold text-white mb-4">הוספת עורך דין יריב</h3>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="block text-blue-200 text-sm mb-2">שם עורך הדין *</label>
            <input
              type="text"
              value={name}
              onChange={(e) => setName(e.target.value)}
              placeholder='עו"ד...'
              className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-500"
              autoFocus
            />
          </div>

          <div>
            <label className="block text-blue-200 text-sm mb-2">משרד</label>
            <input
              type="text"
              value={firm}
              onChange={(e) => setFirm(e.target.value)}
              placeholder="שם המשרד..."
              className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-500"
            />
          </div>

          <div>
            <label className="block text-blue-200 text-sm mb-2">יחס</label>
            <div className="grid grid-cols-3 gap-2">
              {[
                { value: 'friendly', label: 'ידידותי' },
                { value: 'professional', label: 'מקצועי' },
                { value: 'challenging', label: 'מאתגר' },
              ].map(opt => (
                <button
                  key={opt.value}
                  type="button"
                  onClick={() => setSentiment(opt.value as any)}
                  className={`p-2 rounded-lg text-sm transition-all ${
                    sentiment === opt.value
                      ? 'bg-purple-500/30 text-purple-400 border border-purple-500'
                      : 'bg-white/10 text-gray-300'
                  }`}
                >
                  {opt.label}
                </button>
              ))}
            </div>
          </div>

          <div>
            <label className="block text-blue-200 text-sm mb-2">סגנון</label>
            <div className="grid grid-cols-3 gap-2">
              {[
                { value: 'aggressive', label: 'אגרסיבי' },
                { value: 'cooperative', label: 'שיתופי' },
                { value: 'unpredictable', label: 'בלתי צפוי' },
              ].map(opt => (
                <button
                  key={opt.value}
                  type="button"
                  onClick={() => setStyle(style === opt.value ? '' : opt.value as any)}
                  className={`p-2 rounded-lg text-sm transition-all ${
                    style === opt.value
                      ? 'bg-orange-500/30 text-orange-400 border border-orange-500'
                      : 'bg-white/10 text-gray-300'
                  }`}
                >
                  {opt.label}
                </button>
              ))}
            </div>
          </div>

          <div>
            <label className="block text-blue-200 text-sm mb-2">הערות</label>
            <textarea
              value={notes}
              onChange={(e) => setNotes(e.target.value)}
              placeholder="מה כדאי לזכור..."
              className="w-full px-4 py-3 bg-white/10 border border-white/20 rounded-lg text-white placeholder-gray-500 h-20"
            />
          </div>

          <div className="flex gap-3 pt-2">
            <button
              type="button"
              onClick={onClose}
              className="flex-1 px-4 py-2 bg-white/10 text-white rounded-lg hover:bg-white/20"
            >
              ביטול
            </button>
            <button
              type="submit"
              className="flex-1 px-4 py-2 bg-purple-600 text-white rounded-lg hover:bg-purple-700"
            >
              הוסף
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}
